<script lang="coffeescript">var checkTypesForListId, dispatch, draggable, draggableEl, drop, end, getWidth, indentButtonRef, isInvalid, isOver, leave, mousedown, mouseup, over, start, toggleExpanded;

import {
  debug,
  ButtonIcon,
  IconSvg,
  IconToggleSvg,
  Indent
} from "../../../../Braintapper";

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

export var index = void 0;

export var item = void 0;

export var indent = 0;

export var listId = void 0;

export var sameListOnly = false;

indentButtonRef = void 0;

draggableEl = void 0;

draggable = false;

isOver = false;

isInvalid = false;

checkTypesForListId = function(types) {
  return types.find(function(t) {
    return t.startsWith("listid:");
  });
};

start = function(e) {
  item["listId"] = listId;
  item["index"] = index || null;
  e.dataTransfer.setData("text/plain", JSON.stringify(item));
  e.dataTransfer.setData(`listid:${listId}`, JSON.stringify(item));
  return dispatch("dragging", true);
};

end = function(e) { // stop dragging
  console.error("END");
  console.log(e.type);
  //dragStop = ()->
  //   console.log
  return dispatch("dragStop", true);
};


// debouncedFinish = dragStop.debounce(250)
// debouncedFinish()
over = function(e) {
  var draggedListId;
  e.preventDefault();
  isOver = true;
  if (!!sameListOnly) {
    draggedListId = checkTypesForListId(e.dataTransfer.types);
    if (draggedListId != null) {
      return isInvalid = !(listId === draggedListId.replace("listid:", ""));
    } else {
      return isInvalid = true;
    }
  }
};

leave = function(e) {
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, dropped;
  isOver = false;
  isInvalid = false;
  draggable = false;
  e.preventDefault();
  dispatch("dragStop", true);
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  dropped = true;
  if (!!sameListOnly) {
    // console.log "This list: #{listId} :: itemList #{dragItem.listId}"
    dropped = listId === dragItem.listId;
  }
  if (dropped) {
    return dispatch("nest", {
      dragItem: dragItem,
      dragIndex: dragItem.index,
      dragListId: dragItem.listId,
      dropItem: item,
      dropIndex: index,
      dropListId: listId
    });
  }
};

mouseup = function() {
  console.error("MOUSEUP");
  draggable = false;
  return isInvalid = false;
};

mousedown = function() {
  return draggable = true;
};

getWidth = function(el) {
  if (el != null) {
    return el.clientWidth - 48;
  } else {
    return 0;
  }
};

toggleExpanded = function() {
  console.log("toggle Expanded");
  item.is_expanded = !item.is_expanded;
  return dispatch("update", {
    uuid: item.uuid,
    is_expanded: item.is_expanded,
    ancestry: item.ancestry
  });
};

$: draggableWidth = getWidth(draggableEl) ;
</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  bind:this={draggableEl}
  layout="row"
>
      <div flex="initial" handle show-on-hover>
        <div on:mouseup={mouseup(item)} on:mousedown={mousedown(item)}>
          <IconSvg class="handle" icon="handle"/>
          
        </div>      
      </div>

      <div flex class="slot">
          <div layout="row" item-interior>
          <div flex="initial" style="width: {indent * 24}px">
            <Indent level={indent}/>
          </div>
          
          <div flex="initial" collapsible>
          {#if item.list_items.length > 0}
            <ButtonIcon on:click={toggleExpanded}>
              <IconToggleSvg bind:value={item.is_expanded} whenTrue="expanded" whenFalse="collapsed"/>
            </ButtonIcon>
          {/if}
          </div>
          <div flex>
            <slot item={item} index={index}/>
          </div>
        </div>
      </div>
      <div flex="initial" menu draggable-item-menu show-on-hover>
        <slot name="menu"/>
      </div>

</div>
<style global lang="sass">:global([item-draggable]) {
  border-radius: 3px;
  width: calc(100% + 64px);
  max-width: calc(100% + 64px);
  margin-left: -32px;
  box-sizing: border-box;
  padding: 4px 0px;
}
:global([item-draggable]) :global([item-interior]) {
  width: 100%;
  max-width: 100%;
}
:global([item-draggable]) :global([handle]) > :global(div) {
  height: 24px;
  width: 24px;
}
:global([item-draggable]) :global([handle]), :global([item-draggable]) :global([menu]) {
  width: 32px;
}
:global([item-draggable]) :global([menu]) {
  text-align: center;
}
:global([item-draggable]) :global(.slot) {
  width: 100%;
  max-width: 100%;
}
:global([item-draggable]) :global([show-on-hover]) :global(svg) :global(path) {
  fill: transparent;
}
:global([item-draggable]) :global([show-on-hover][is-showing=true]) :global(svg) :global(path) {
  fill: var(--blue) !important;
}
:global([item-draggable]) :global([collapsible]) {
  width: 24px;
}
:global([item-draggable]) :global([collapsible]) :global(svg), :global([item-draggable]) :global([collapsible]) :global(svg.menu) {
  display: inline;
}
:global([item-draggable]) :global([handle]) > :global(div:hover) {
  cursor: grab;
}
:global([item-draggable].over) {
  background: #dddddd;
}
:global([item-draggable].invalid) {
  background: var(--dark-red) !important;
}
:global([item-draggable]:hover) {
  background: #fcfcfc;
}
:global([item-draggable]:hover) :global([handle]) :global(svg), :global([item-draggable]:hover) :global([handle]) :global(svg.menu), :global([item-draggable]:hover) :global([collapsible]) :global(svg), :global([item-draggable]:hover) :global([collapsible]) :global(svg.menu), :global([item-draggable]:hover) :global([draggable-item-menu]) :global(svg), :global([item-draggable]:hover) :global([draggable-item-menu]) :global(svg.menu) {
  display: inline;
}
:global([item-draggable]:hover) :global([show-on-hover]) :global(svg) :global(path) {
  fill: var(--gray);
}
:global([item-draggable]:hover) :global([show-on-hover]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}
:global([item-draggable]:hover) :global([handle]) :global(svg) :global(path) {
  fill: var(--light-gray);
}
:global([item-draggable]:hover) :global([collapsible]) :global(button[icon]) {
  padding: 0px !important;
}
:global([item-draggable]:hover) :global([collapsible]) :global(svg) :global(path.fill) {
  fill: var(--light-gray);
}
:global([item-draggable]:hover) :global([collapsible]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}</style>
