<script lang="coffeescript">var addDispatcher, cloneListItem, destroyDispatcher, dispatch, dropDispatcher, goToItem, message, nestDispatcher, onDrag, onDragStop, style, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import NestableItem from "./Sections/Nestable/NestableItem.svelte";

import NestableDivider from "./Sections/Nestable/NestableDivider.svelte";

import {
  debug,
  IconSvg,
  InlineInput
} from "../../Braintapper";

import {
  ListItem,
  ListItemMenu,
  ProgressPopover,
  DueDatePopover
} from "../../Application";

export var list = [];

export var dragState = void 0;

message = "";

style = ":hover {font-weight: bold}";

goToItem = function(childItem) {
  return location.href = `#/lists/${childItem.id}`;
};

updateListItem = function(e) {
  return dispatch("update", e.detail);
};

cloneListItem = function(e) {
  return dispatch("clone", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};

// drag and drop
dropDispatcher = function(e) {
  return dispatch("drop", e.detail);
};

nestDispatcher = function(e) {
  return dispatch("nest", e.detail);
};

addDispatcher = function(e) {
  console.log("addDispatcher");
  return dispatch("add", e.detail);
};

onDrag = function(e) {
  return dispatch("dragging", true);
};

onDragStop = function(e) {
  console.log(e);
  console.log("dragStop");
  return dispatch("dragStop", true);
};
</script>
{#if (list.type != "RootItem") && (list.list_items.length > 0)}
  <NestableDivider item={{ ancestry: list.ancestry.clone().append(list.uuid), index: -1} } indentation={list.ancestry.length + 1}  on:drop={dropDispatcher} on:nest={nestDispatcher} firstItem={true} on:add={addDispatcher} index={-1} bind:dragState={dragState}/>
{/if}
{#each list.list_items as listItem, index}


  <NestableItem item={listItem} index={index} indent={listItem.ancestry.length} on:nest={nestDispatcher} on:update={updateListItem} on:dragging={onDrag} on:dragStop={onDragStop}>
    <div layout="row" type="list-item">

      <div flex="initial" section="progress"> 
        <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
      </div>

      <div flex ellipsis main>
        <InlineInput bind:value={listItem.name} on:update={updateListItem({ detail: { uuid: listItem.uuid, ancestry: listItem.ancestry, name: listItem.name  } })}  let:label>
          {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
          {:else}<span ellipsis>{label || "No Name"}</span>&nbsp;
            {#if listItem.is_ongoing}
              <span><IconSvg icon="ongoing" size="12"/></span>
            {/if}
          {/if}
        </InlineInput>

      </div>
      <div flex="initial">
        <div layout="row">
          <!-- due date menu -->
          <div flex="initial" section="due-date" present={listItem.due_date != null}>
            <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
          </div>
        </div>
      </div>

    </div>

    <div slot="menu">
      <ListItemMenu bind:item={listItem} on:update={updateListItem} on:clone={cloneListItem} on:destroy={destroyDispatcher}/>
    </div>

  </NestableItem>
  {#if (listItem.list_items.length == 0) || (!listItem.is_expanded)}
  <NestableDivider item={{ ancestry: listItem.ancestry.clone().append(listItem.uuid), list_items: [], index: -1}} indentation={listItem.ancestry.length + 1} on:drop={dropDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
   <div style="height: 4px;"/>
  {/if}
  <!-- children -->
  {#if listItem.is_expanded }
    <svelte:self bind:list={listItem}  bind:dragState={dragState} on:update={updateListItem} on:drop={dropDispatcher} on:clone={cloneListItem} on:nest={nestDispatcher} on:destroy={destroyDispatcher} on:add={addDispatcher} on:dragging={onDrag} on:dragStop={onDragStop}/>
  {/if}
  <!-- don't show when last item of root -->
  {#if !((list.type == "RootItem") && (index == (list.list_items.length - 1)))}
    <NestableDivider item={listItem} indentation={listItem.ancestry.length} on:drop={dropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
  {/if}


{/each}


<style lang="sass">[type=list-item] {
  width: 100%;
  max-width: 100%;
}
[type=list-item] [section=progress] {
  padding-right: 8px;
}
[type=list-item] [main] {
  width: 100px;
}
[type=list-item]:hover [present] {
  display: flex;
}</style>
