<script lang="coffeescript">// /lists
// List Index
var addRootItem, cloneRootItem, destroy, goToItem, itemUrl, processList, refreshList, resourceList, selectAndRefresh, toggleFavorite, updateRootItem;

import {
  push,
  pop,
  replace,
  location
} from "svelte-spa-router";

import {
  // Data Items
  debug,
  ButtonIcon,
  IconToggleSvg,
  InlineInput
} from "../../Braintapper";

import {
  RootItemList,
  RootItem,
  PageContent,
  PageHeader,
  ProgressPopover,
  DueDatePopover,
  QuickEntry,
  ListMenu
} from "../../Application";


// Local Vars
export var list = [];

export var options = {};

export var context = "current";

// List Model
resourceList = new RootItemList;

refreshList = function(items) {
  return list = items;
};

// jumps
itemUrl = "#/lists/"; // links and redirects

goToItem = function(item) {
  return push(`#/lists/${item._id}`);
};

toggleFavorite = function(rootItemList) {
  //console.log "toggle Favorite"
  //console.log rootItemList
  rootItemList.is_favorite = !rootItemList.is_favorite;
  list = list;
  return updateRootItem({
    detail: {
      id: rootItemList._id,
      is_favorite: rootItemList.is_favorite
    }
  });
};

// CRUD
cloneRootItem = function(e) {
  var resource, scb;
  resource = new RootItem;
  scb = function(doc) {
    var clone;
    return clone = resource.clone();
  };
  return resource.get(e.detail._id, scb, null, null);
};

addRootItem = function(e) {
  var scb;
  scb = function(inserted) {
    return push(`/lists/${inserted._id // redirect to list page
}`);
  };
  return resourceList.insert(e.detail, scb, null, null);
};

selectAndRefresh = function(view) {
  switch (view) {
    case "archived":
      return resourceList.selectArchived(refreshList); // "Lists"
    default:
      return resourceList.select(refreshList);
  }
};

updateRootItem = function(e) {
  var ecb;
  ecb = function(err) {};
  // refreshList()
  return resourceList.patch(e.detail, selectAndRefresh, ecb, null);
};

destroy = function(e) {
  var ecb;
  ecb = function(e) {
    console.log("destroy error");
    return console.log(e);
  };
  return resourceList.destroy(e.detail._id, selectAndRefresh, ecb);
};

// UI
processList = function(list, options) {
  var filteredList, sortableList;
  sortableList = list.map(function(o) {
    var surrogateDate;
    // because due_date can be null, and because 
    // sortBy has issues with nulls
    // need to create a sort field
    if (options.sort_by === "due_date") {
      // always keep nulls at the bottom
      if (options.sort_descending) {
        surrogateDate = "0000-00-00";
      } else {
        surrogateDate = "9999-99-99";
      }
      o["sort_field"] = o[options.sort_by] || surrogateDate;
    } else {
      o["sort_field"] = o.progress;
    }
    return o;
  });
  filteredList = sortableList.filter(function(listItem) {
    var result;
    result = true;
    if (options.favorites_only) {
      result = result && listItem.is_favorite;
    }
    if (!options.include_completed) {
      result = result && (listItem.progress.percent_complete < 100);
    }
    return result;
  });
  if (list != null) {
    return filteredList.sortBy("sort_field", options.sort_descending);
  } else {
    return [];
  }
};

$: sortedlist = processList(list, options);

$: selectAndRefresh(context);
</script>



{#if list && list.length}
  {#each sortedlist as item, index}

      <div layout="row" type="list">
        <div flex="initial" favorite class:favorited={item.is_favorite}>
          <ButtonIcon on:click={toggleFavorite(item)}>
            <IconToggleSvg bind:value={item.is_favorite} whenTrue="star" whenFalse="star-empty" />
          </ButtonIcon>                
        </div>            
        <div flex="initial" progress>
          <ProgressPopover {item} bind:value={item.progress} on:update={updateRootItem} on:open on:close idAttribute="_id"/>
        </div>

        <div flex ellipsis>
          <InlineInput bind:value={item.name} on:update={updateRootItem( { detail: { _id: item._id, name: item.name } })} on:clicked={goToItem(item)} let:label>
            <span ellipsis>
              <a href="{itemUrl}{item._id}" on:click|preventDefault><em>{label || "No Name"}</em></a>&nbsp;
            </span>
          </InlineInput>

        </div>
        <div flex="initial">
          <div layout="row">
            <div flex="initial" present={item.due_date != null}>
              <DueDatePopover {item} value={item.due_date} on:update={updateRootItem} idAttribute="_id" on:open on:close/>
            </div>

          </div>
        </div>
        <div flex="initial" menu show-on-hover>
          <ListMenu {item} on:select={updateRootItem} on:clone={cloneRootItem} on:destroy={destroy}/>

        </div>
      </div>
    
  {/each}
{/if}

<style global lang="sass">:global(header) :global(h1) {
  font-size: 36px;
  color: var(--darkest-blue);
}

:global([type=list]) {
  border-radius: 3px;
  width: calc(100% + 64px);
  max-width: calc(100% + 64px);
  margin-left: -32px;
  margin-bottom: 8px;
  box-sizing: border-box;
  padding: 4px 0px;
}
:global([type=list]) :global([item-interior]) {
  width: 100%;
  max-width: 100%;
}
:global([type=list]) :global([favorite]), :global([type=list]) :global([menu]), :global([type=list]) :global([progress]) {
  width: 32px;
}
:global([type=list]) :global([favorite]) :global(svg) :global(path) {
  fill: transparent;
}
:global([type=list]) :global([favorite].favorited) :global(svg) :global(path) {
  fill: var(--lightest-gray);
}
:global([type=list]) :global([menu]) {
  text-align: center;
}
:global([type=list]:hover) :global([favorite]) :global(svg) :global(path) {
  fill: var(--blue);
}
:global([type=list]) :global(.slot) {
  width: 100%;
  max-width: 100%;
}
:global([type=list]) :global([show-on-hover]) :global(svg) :global(path) {
  fill: transparent;
}
:global([type=list]:hover) {
  background: #fcfcfc;
}
:global([type=list]:hover) :global([show-on-hover]) :global(svg) :global(path) {
  fill: var(--gray);
}
:global([type=list]:hover) :global([show-on-hover]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}</style>
