<script lang="coffeescript">var destroyDispatcher, dispatch, goToList, toggleExpanded, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  debug,
  ButtonIcon,
  IconSvg,
  IconToggleSvg,
  Indent,
  InlineInput
} from "../../Braintapper";

import {
  ProgressPopover,
  DueDatePopover,
  ListItemMenu
} from "../../Application";

export var list = void 0;

export var listId = void 0;

goToList = function() {
  return location.href = `#/lists/${listId}`;
};

toggleExpanded = function(item) {
  console.log("toggle Expanded");
  item.is_expanded_radar = !item.is_expanded_radar;
  return dispatch("update", {
    uuid: item.uuid,
    is_expanded_radar: item.is_expanded_radar,
    ancestry: item.ancestry,
    list_id: listId
  });
};

updateListItem = function(e) {
  if (listId != null) {
    console.log(`top event listid: ${listId}`);
    e.detail["list_id"] = listId;
  }
  return dispatch("update", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};
</script>


{#each list.list_items as listItem, index}
  
    
    <div type="radar-item">
      <div flex="initial" style="width: {listItem.ancestry.length * 32}px">
        <Indent level={listItem.ancestry.length} width={32}/>
      </div>
          
      <div collapsible>

      {#if listItem.list_items.length > 0}
        <ButtonIcon on:click={toggleExpanded(listItem)}>
          <IconToggleSvg bind:value={listItem.is_expanded_radar} whenTrue="expanded" whenFalse="collapsed"/>
        </ButtonIcon>
      {/if}
      </div>    
      <div layout="row" flex>
        <div layout="row" item-interior>


          <div flex="initial" progress>
            <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
          </div>

          <div flex ellipsis main>
            
              {#if listItem.is_hidden}<span ellipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</span>
              {:else}<span ellipsis>{listItem.name || "No Name"}</span>&nbsp;
              {/if}            
              {#if listItem.is_ongoing}
                &nbsp;
                <span><IconSvg icon="ongoing" size="12"/></span>
              {/if}
            

          </div>
          <div flex="initial">

            <!-- due date menu -->
            
            <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close idAttribute="uuid"/>
            
          </div>
        </div>
      </div>
      <div flex="initial" menu show-on-hover>
        <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar">
          <IconSvg icon="menu" classes="menu"/>
        </ListItemMenu>
      </div>


    </div>



    <!-- children -->
    {#if listItem.is_expanded_radar }
      <svelte:self bind:list={listItem} {listId} on:update={updateListItem} on:destroy={destroyDispatcher} />
    {/if}
  
{/each}


