}
:global([type=radar-item]) :global([menu]) {
  text-align: center;
}
:global([type=radar-item]) :global(.slot) {
  width: 100%;
  max-width: 100%;
}
:global([type=radar-item]) :global([show-on-hover]) :global(svg) :global(path) {
  fill: transparent;
}
:global([type=radar-item]) :global([show-on-hover][is-showing=true]) :global(svg) :global(path) {
  fill: var(--blue) !important;
}
:global([type=radar-item]) :global([collapsible]) {
  width: 32px;
  line-height: 24px;
}
:global([type=radar-item]) :global([collapsible]) :global(svg), :global([type=radar-item]) :global([collapsible]) :global(svg.menu) {
  display: inline;
}
:global([type=radar-item]:hover) {
  background: #fcfcfc;
}
:global([type=radar-item]:hover) :global([show-on-hover]) :global(svg) :global(path) {
  fill: var(--gray);
}
:global([type=radar-item]:hover) :global([show-on-hover]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}
:global([type=radar-item]:hover) :global([handle]) :global(svg), :global([type=radar-item]:hover) :global([handle]) :global(svg.menu), :global([type=radar-item]:hover) :global([collapsible]) :global(svg), :global([type=radar-item]:hover) :global([collapsible]) :global(svg.menu), :global([type=radar-item]:hover) :global([draggable-item-menu]) :global(svg), :global([type=radar-item]:hover) :global([draggable-item-menu]) :global(svg.menu) {
  display: inline;
}
:global([type=radar-item]:hover) :global([show-on-hover]) :global(svg) :global(path) {
  fill: var(--gray);
}
:global([type=radar-item]:hover) :global([show-on-hover]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}
:global([type=radar-item]:hover) :global([collapsible]) :global(button[icon]) {
  padding: 0px !important;
}
:global([type=radar-item]:hover) :global([collapsible]) :global(svg) :global(path.fill) {
  fill: var(--light-gray);
}
:global([type=radar-item]:hover) :global([collapsible]:hover) :global(svg) :global(path) {
  fill: var(--blue);
}</style>
